<template>
  <div>
    <v-simple-table>
      <template v-slot:default>
        <thead>
          <tr>
            <th class="text-left">Id</th>
            <th class="text-left">Name</th>
            <th class="text-left">img</th>
            <th class="text-right">Action</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="type in types" :key="type.id">
            <td>{{ type.id }}</td>
            <td>{{ type.name }}</td>
            <td>{{ type.img }}</td>
            <td style="text-align:right">
              <v-btn
                :to="{
                  name: 'editType',
                  params: {
                    id: type.id
                  }
                }"
                small
                class="mr-2"
                >Edit</v-btn
              >
              <v-btn small dark @click="deleteType(type.id)">Delete</v-btn>
            </td>
          </tr>
        </tbody>
      </template>
    </v-simple-table>

    <div class="text-center ma-2"></div>
  </div>
</template>

<script>
import typeService from "../api/typeService.js";
export default {
  mounted() {
    typeService.getAllTypes().then(response => {
      this.types = response.data;
      console.log(this.types);
    });
  },
  data() {
    return {
      types: []
    };
  },

  methods: {
    async deleteType(typeId) {
      const conf = confirm("are you sure to deletet this?");
      if (conf) {
        const response = await typeService.deleteType(typeId);

        console.log(response.data);
        this.types = this.types.filter(type => {
          return type.id !== typeId;
        });
      }
    }
  }
};
</script>
<style>
th,
td {
  border: 1px solid gray;
}
</style>
