<template>
  <div>
    <v-row>
      <!-- 


 -->

      <v-text-field label="typeId" outlined v-model="typeId"></v-text-field>

      <!--  -->
      <v-col cols="12" sm="3" class="dark--text"><h1>Edit</h1></v-col>
    </v-row>

    <div class="d-flex">
      <v-card style="min-width:800px;max-width:400px;">
        <v-col cols="12" sm="12" md="12">
          <v-text-field label="Name" outlined v-model="name"></v-text-field>
        </v-col>

        <v-col cols="12" sm="12" md="12">
          <v-text-field label="Email" outlined v-model="img"></v-text-field>
        </v-col>

        <v-col class="text-right ">
          <v-btn text color="green" @click="edithType">Edit </v-btn>
        </v-col>
      </v-card>
    </div>
  </div>
</template>

<script>
import typeService from "../api/typeService";

export default {
  data() {
    return {
      name: null,
      img: null,
      typeId: this.$route.params.id
    };
  },

  async created() {
    if (this.typeId) {
      const answer = await typeService.getType(this.typeId);
      const res = answer.data;
      console.log(res);
      console.log("asdfsdf");
      this.name = res.name;
      this.img = res.img;
    }
  },

  methods: {
    async edithType() {
      const type1 = {
        id: this.typeID,
        name: this.name,
        img: this.img
      };

      const savedComment = await typeService.updateType(type1);

      console.log(savedComment.data);
    }
  }
};
</script>

