import Vue from "vue";
import VueRouter from "vue-router";

import addType from "../views/add.vue";
import showTypes from "../views/show.vue";
import editType from "../views/edit.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/editType",
    name: "editType",
    component: editType
  },
  {
    path: "/showTypes",
    name: "showTypes",
    component: showTypes
  },

  {
    path: "/addType",
    name: "addType",
    component: addType
  }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,

  routes
});

export default router;
