<template>
  <v-container>
    <h1>add</h1>
    <div class="d-flex">
      <v-card style="min-width:800px;max-width:400px;">
        <v-col cols="12" sm="12" md="12">
          <v-text-field label="Name" outlined v-model="name"></v-text-field>
        </v-col>

        <v-col cols="12" sm="12" md="12">
          <v-text-field label="Email" outlined v-model="img"></v-text-field>
        </v-col>

        <v-col class="text-right ">
          <v-btn text color="green" @click="addType">Add </v-btn>
        </v-col>
      </v-card>
    </div>
  </v-container>
</template>

<script>
import typeService from "../api/typeService.js";

export default {
  component: {},

  data() {
    return {
      name: null,
      img: null
    };
  },

  methods: {
    async addType() {
      const type1 = {
        name: this.name,
        img: this.img
      };
      console.log(type1);
      const savedType = await typeService.addType(type1);

      console.log("type saved to database");
      console.log(savedType);
      this.resetform();
    },

    resetform() {
      this.name = null;
      this.img = null;
    }
  }
};
</script>
